<template>
  <div class='container'>
   <h1>Latest Posts</h1>
   <div class="create-pos">
     <label for="create-post">Say Something...</label>
     <input type="text" v-model='text' placeholder="Create a post">
     <button @click='createPost' >Post!</button>
   </div>
   <hr/>
   <p class='error' v-if='error'>{{error}}</p>
   <div class="posts-container">
     <div class="post" 
      v-for='(post, index) in posts'
      :item='post'
      :index='index'
      :key='post._id'
      >
      <p class='text'> {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}</p>
      <p class='text'>{{post.text}}</p>
      <button @click='deletePost(post._id)'>Delete Post</button>
      <button @click='showEditPost'>Edit this post</button>
      <div v-if=(editPostDiv)>
        <label for="edit-post">Edit Post Content</label>
        <input type="text" name='edit-post' v-model='editText'>
        <button @click='confirmEdit(post._id)'>Confirm Edit</button>
      </div>
     </div>
   </div> 
  </div>
</template>

<script>
import PostService from '../PostService.js';

export default {
  name: 'PostComponent',
  data() {
    return {
      posts: [],
      error: '',
      text: '',
      editPostDiv: false,
      editText: '',
    }
  },
  computed: {
    fillEditField() {

    }
  },
  methods: {
    showEditPost() {
      this.editPostDiv = !this.editPostDiv
    },
    async confirmEdit(id) { 
       PostService.editPost(id, this.editText)
      this.posts = await PostService.getPosts();
      this.showEditPost();
    },
    async createPost() {
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
      this.text = '';
    },
    async deletePost(id) {
      await PostService.deletePost(id)
      this.posts = await PostService.getPosts();
    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  }
}
</script>

