import axios from 'axios';

const url = 'api/posts/';

export default class PostService {
  // Get Posts
  // static is a bypass for having to instantiate a new PostService class
  // ust call PostService.getPosts() where you need it
  static getPosts() {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(url);
        const data = res.data;
        resolve(
          data.map(post => ({
            ...post,
            createdAt: new Date(post.createdAt)
          }))
        );
      } catch(err) {
        reject(err);
      }
    })
  }

  // Create Posts
  static insertPost(text) {
    return axios.post(url, {
      text
    });
  }
  // Delete Posts
  static deletePost(id) {
    return axios.delete(`${url}${id}`);
  }
  // Update Posts
  static editPost(id, updatedText) {
    return axios.put(`${url}${id}`, {
      text: updatedText
    }).then(response => {
      console.log(response)
    }).catch(err => {
      console.log(err)
    });

  }


}